.grid-box {
    position: relative;
}

.grid-box-img {
    position: relative;
    overflow: hidden;
}

.grid-box>a {
    display: block;
    position: relative;
    overflow: hidden;
    color: #ffffff;
    border-radius: 0px;
}

.grid-ov,
.grid-content {
    position: absolute;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
    transition: all 0.2s linear;
    z-index: 2;
    display: flex;
    justify-content: center;
    align-items: center;
}

.grid-content.alt {
    height: inherit;
    padding: 30px 30px;
    bottom: 0px;
    top: auto;
    z-index: 2;
}

.grid-content {
    padding: 30px;
}

.grid-info {
    padding-top: 25px;
}

.grid-ov {
    opacity: 0;
    visibility: hidden;
    z-index: 9;
    padding: 30px 30px;
    overflow-y: auto;
}

.grid-box:hover .grid-content {
    visibility: hidden;
    opacity: 0;
}

.grid-box:hover .grid-ov {
    opacity: 1;
    visibility: visible;
    z-index: 9;
}

.grid-ov-info {}

.grid-content-info {}

.grid-tb {
    display: table;
    height: 100%;
    width: 100%;
}

.grid-tc {
    display: table-cell;
    vertical-align: middle;
}

.grid-tc.bt {
    vertical-align: bottom;
}

.grid-tc.tp {
    vertical-align: top;
}

.grid-tc img {
    vertical-align: middle;
}

.grid-box .img-holder::before {
    position: absolute;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
    content: "";
    z-index: 2;
    transition: all 0.2s linear;
    opacity: .2;
    background: #000000;
    /*background: url(../images/overlay2.png); background-size: 100% 100%;*/
}

.grid-box .img-holder::after {
    position: absolute;
    left: 0px;
    bottom: 0px;
    width: 100%;
    height: 50%;
    content: "";
    z-index: 1;
    transition: all 0.2s linear;
    /*background: url(../images/overlay1.png); background-size: 100% 100%;*/
    /*opacity: .95;background:#0A394B;*/
}

.grid-box .img-holder {
    border: 0;
}

.grid-box:hover .img-holder::before {
   opacity: .8;
    background: #025991;
}

.grid-box:hover .img-holder::after {
    opacity: 0;
    background: url(../images/overlay2.html);
    background-size: 100% 100%;
}
.img-holder { position: relative; display: block; width: 100%; height: 0px; overflow: hidden; padding-bottom: 100%; margin: 0px auto 0px; }
.img-holder figure { position: absolute; top: 0px; left: 0px; right: 0px; bottom: 0px; display: block; margin: 0px; text-align: center; padding: 0px; }
.img-holder figure img { position: relative; width: 100%; height: 100%; max-width: none; max-height: none; z-index: 1; transition: all 0.6s; object-fit: scale-down; object-position: 50% 50%; }
.img-holder.img-cover figure img { object-fit: cover; }
.img-effect { overflow: hidden; display: block; position: relative; }
.img-effect:hover .img-holder figure img { /*transform: scale(1.1);*/ transform: scale(1.1) rotate(2deg); }
.fancybox-thumbs__list a::before { border-color: var(--primary); border-width: 3px; }

/*--Grid-Border-Effect--*/
.grid-ov, .grid-content {transition: all 1s;}
